import React, { useEffect, useState } from "react";
import { Button, Modal, Input, Spin } from "antd";
import moment from "moment-jalaali";

moment.loadPersian({ dialect: "persian-modern" });

const AnnouncementModule = ({
    isModalOpen,
    setIsModalOpen,
    setToastifyObj,
    announcementData,
}) => {
    const [textAreaContent, setTextAreaContent] = useState(
        "Aloha, Nothing to see here"
    );

    const convertToPersianNumbers = (str) => {
        const persianNumbers = "";
        return str.replace(
            /[0-9]/g,
            (char) => persianNumbers[parseInt(char, 10)]
        );
    };

    useEffect(() => {
        if (announcementData.startWeekDate) {
            const startWeekDate = moment(
                announcementData.startWeekDate,
                "YYYY/M/D"
            ).format("jD jMMMM");

            const endWeekDate = moment(
                announcementData.endWeekDate,
                "YYYY/M/D"
            ).format("jD jMMMM");

            const firstEventDate = moment(
                announcementData.firstEventDate,
                "YYYY/M/D"
            ).format("jD jMMMM");

            const secondEventDate = moment(
                announcementData.secondEventDate,
                "YYYY/M/D"
            ).format("jD jMMMM");

            setTextAreaContent(`    

     (${convertToPersianNumbers(
                startWeekDate
            )}  ${convertToPersianNumbers(
                endWeekDate
            )})         :

1  ${convertToPersianNumbers(firstEventDate)}
: ${announcementData?.firstEvent?.title.replace(/:/g, " -")}

2  ${convertToPersianNumbers(secondEventDate)}
: ${announcementData?.secondEvent?.title.replace(/:/g, " -")}

  : :  :

  :   :  : (     )

                           .

.`);
        }
    }, [announcementData]);

    const handleCancel = () => {
        setIsModalOpen(false);
    };

    const handleCopyMessage = async () => {
        try {
            await navigator.clipboard.writeText(textAreaContent);

            setToastifyObj(() => ({
                title: "    .",
                mode: "success",
            }));
        } catch (error) {
            setToastifyObj(() => ({
                title: "       .",
                mode: "error",
            }));
        }
    };

    return (
        <Modal
            open={isModalOpen}
            onCancel={handleCancel}
            footer={[
                <Button key="back" onClick={handleCancel} className="close-btn">
                    
                </Button>,
                <Button
                    key="submit"
                    type="primary"
                    onClick={handleCopyMessage}
                    className="submit-btn"
                >
                     
                </Button>,
            ]}
            width="842px"
            closeIcon={false}
            className="modal-container code-modal"
        >
            <div className="modal-header">  </div>
            <div className="modal-title">
                             
                  .
            </div>

            {textAreaContent !== "Aloha, Nothing to see here" ? (
                <Input.TextArea
                    rows={18}
                    value={textAreaContent}
                    onChange={(e) => setTextAreaContent(e.target.value)}
                    placeholder="    ..."
                    className="announcement-textarea"
                />
            ) : (
                <Spin size="large" className="loading-spinner" />
            )}
        </Modal>
    );
};

export default AnnouncementModule;
